
*[data-v-d66600dc] {
  font-family: '微軟正黑體';
  margin: auto;
}
.container[data-v-d66600dc] {
  padding: 10px 30px 0px 0px;
}
.name[data-v-d66600dc] {
  text-align: center;
  margin: 10px 50px 10px 0px;
  color: #333333;
}
.reset[data-v-d66600dc] {
  margin-top: 10px;
  padding: 5px 10px;
  border-radius: 10px;
  font-weight: 600;
  color: #333333;
  background-color: #CCCCCC;
  display: inline-block;
  cursor: pointer;
}
.chatRoom[data-v-d66600dc] {
  border-radius: 5px;
  max-width: 500px;
  -webkit-box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
          box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

/* Head */
.roomHead[data-v-d66600dc] {
  width: 100%;
  height: 85px;
  border-radius: 5px 5px 0px 0px;
  background-color: #2B364B;
  position: relative;
}
.roomHead__topButtons[data-v-d66600dc] {
  padding: 2px 0px 5px 10px;
  text-align: left;
}
.roomHead__button[data-v-d66600dc] {
  height: 10px;
  width: 10px;
  border-radius: 50%;
  margin: 0px 1px;
  display: inline-block;
  cursor: pointer;
}
.close[data-v-d66600dc] {
  background-color: #FF625A;
}
.minimize[data-v-d66600dc] {
  background-color: #FFC02F;
}
.zoom[data-v-d66600dc] {
  background-color: #28CB40;
}
.roomHead__img[data-v-d66600dc] {
  width: 50px;
  height: 50px;
  margin: 0px 10px 6px 12px;
  border-radius: 50%;
  position: absolute;
  cursor: pointer;
}
.roomHead__title[data-v-d66600dc] {
  font-size: 13px;
  font-weight: 600;
  color: #FFFFFF;
  height: 80px;
  margin: 5px 0px 0px 75px;
  position: absolute;
  cursor: pointer;
}

/* Body */
.roomBody[data-v-d66600dc] {
  padding: 10px 0px;
  background-color: #fff;
  height: 60vh;
  overflow-y: auto;
  overflow-x: hidden;
}
.messageBox[data-v-d66600dc] {
  padding: 5px 10px;
  position: relative;
}
.messageBox__user[data-v-d66600dc] {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  vertical-align: top;
  display: inline-block;
  cursor: pointer;
}
.messageBox__content[data-v-d66600dc] {
  max-width: 70%;
  display: inline-block;
}
.messageBox__name[data-v-d66600dc] {
  margin: 5px 0px 5px 5px;
  font-size: 13px;
  color: #727C8A;
  vertical-align: top;
  cursor: pointer;
}
.messageBox__message[data-v-d66600dc]{
  margin: 5px 0px 5px 5px;
  padding: 8px 10px 7px 11px;
  font-size: 12px;
  letter-spacing: 0.6px;
  background-color: #E3E8EB;
  border-radius: 12px;
  line-height: 1.5;
  text-align: left;
  word-break: break-all;
  /*：與html的<pre></pre>同效果，可以使textarea的換行元素正常顯示 */
  white-space: pre-line;
}
.messageBox__image[data-v-d66600dc] {
  margin: 5px 25px 5px 5px;
}
.messageBox__image img[data-v-d66600dc] {
  border-radius: 5px;
  max-width: 100%;
  max-height: 335px;
}
.messageBox__time[data-v-d66600dc] {
  -webkit-transform: scale(0.7);
          transform: scale(0.7);
  color: #ACB0B8;
  vertical-align: bottom;
  margin: 0px 0px 5px -12px;
  display: inline-block;
}
.messageBox__progress[data-v-d66600dc] {
  width: 25%;
  margin-right: 60px;
  border-radius: 5px;
  background-color: #D4D9E1;
  height: 6px;
}
.messageBox__progress--state[data-v-d66600dc] {
  background-color: #00CE00;
  height: 6px;
  width: 10%;
  border-radius: 5px;
  float: right;
}
.messageBox__progress--number[data-v-d66600dc] {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 12px;
  color: #B7B7B7;
  margin-right: 35px;
}
.messageBox--self[data-v-d66600dc] {
  text-align: right;
}
.messageBox--self .messageBox__message[data-v-d66600dc] {
  background-color: #AFF47E;
  margin-right: 25px;
}
.messageBox--self .messageBox__time[data-v-d66600dc] {
  margin: 0px -16px 5px 0px;
}

/* Bottom */
.roomBottom[data-v-d66600dc] {
  bottom: 0px;
  border-radius: 0px 0px 5px 5px;
  background-color: #FFFFFF;
}
.roomBottom__tools[data-v-d66600dc] {
  border-top: solid 1px #E7E7E7;
  border-bottom: solid 2px #E7E7E7;
  background-color: #F6F6F6;
  height: 30px;
  padding: 0px 5px;
}
.roomBottom__tools_upload[data-v-d66600dc] {
  height: 20px;
  margin: 5px;
  padding: 1px;
  display: inline-block;
  position: relative;
  overflow: hidden;
}
.roomBottom__tools_upload[data-v-d66600dc]:hover {
  border: solid 1px #DCDCDC;
}
.roomBottom__tools_upload input[data-v-d66600dc] {
  width: 100%;
  height: 100%;
  left: 0;
  right: 0;
  opacity: 0; 
  position: absolute;
  cursor: pointer;
  /* 讓input file可以支援pointer要加pl100% */
  padding-left: 100%;
}
.roomBottom__tools_upload img[data-v-d66600dc] {
  height: 100%;
}
.roomBottom__input[data-v-d66600dc] {
  padding: 10px 10px 5px 10px;
}
.roomBottom__input__textarea[data-v-d66600dc] {
  width: 100%;
  height: 60px;
  border: none;
  resize: none;
  outline: none;
}

/* status */
.disable[data-v-d66600dc] {
  pointer-events: none;
  background-color: #ebebeb;
}

/* modal */
.modal[data-v-d66600dc] {
  z-index: 3;
  padding: 50px 0px;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
  -webkit-animation: opac 0.8s;
          animation: opac 0.8s;
  letter-spacing: 2px;
  text-align: unset;
}
.modal__container[data-v-d66600dc] {
  margin: auto;
  position: relative;
  padding: 10px;
  outline: 0;
  max-width: 300px;
}
.modal__header[data-v-d66600dc] {
  color: #fff;
  background-color: #2B364B;
  padding: 10px;
  text-align: center;
  border-radius: 5px 5px 0px 0px;
}
.modal__body[data-v-d66600dc] {
  background-color: #fff;
  padding: 20px 50px;
  text-align: center;
}
.modal__body p[data-v-d66600dc] {
  text-align: left;
  line-height: 24px;
}
.modal__img[data-v-d66600dc] {
  max-width: 100%;
}
.modal__footer[data-v-d66600dc] {
  color: #fff;
  background-color: #2B364B;
  height: 8px;
  border-radius: 0px 0px 5px 5px;
}

/* name set */
.userName[data-v-d66600dc] {
  height: 30px;
  font-size: 16px;
  margin-bottom: 10px;
  border: solid 1px #cbcbcb;
  width: 70%;
  text-align: center;
  display: inline-block;
}
.button[data-v-d66600dc] {
  font-size: 14px;
  color: #FFFFFF;
  background-color: #2B364B;
  padding: 10px 20px;
  display: inline-block;
  cursor: pointer;
}

/* media */
@media screen and (max-width: 425px) {
.messageBox__content[data-v-d66600dc] {
    max-width: 60%;
}
}
@media screen and (max-width: 385px) {
.messageBox__content[data-v-d66600dc] {
    max-width: 50%;
}
}



